const suggesionsTemplate = (data) => `
        <a class="list-items flex gap-2 items-center bg-slate-900 rounded mr-2 relative hover:-translate-y-1 transition-all" href="/#/${data.media}/${data.id}" title="${data.name ? data.name : data.title}">
            <div class="poster">
                <img class="object-cover w-[90px] rounded-tl rounded-bl" src="https://image.tmdb.org/t/p/w185${data.poster_path}" alt="image og ${data.name ? data.name : data.title}" loading="lazy">
            </div>
            <div class="title w-full">
                <h2 class="font-medium">${data.name ? data.name : data.title}</h2>
                <!---<i class="text-xs">${String(data.overview).substring(0, 30)}...</i>--->
            </div>
            <div class="rating flex items-center mr-5">
                <span class="text-xs bg-red-600 text-slate-50 py-1 px-2 rounded font-medium">${String(data.vote_average).substring(0, 3)}/10</span>
            </div>
        </a>
    `;

const listTemplate = (judul, pagesName) => `
            <div class="text-slate-50 bg-slate-800 p-2 ${pagesName === 'genre' || pagesName === 'on-air' || pagesName === 'top-rated-tv' || pagesName === 'popular-tv-show' ? '' : 'mt-5'} rounded">
                <div class="title mb-2 flex items-center justify-between px-2">
                    <h2 class="font-bold text-lg">${pagesName === 'genre' ? `Result for ${judul}` : judul}</h2>
                </div>
                <div class="${judul === 'Now Playing Movies' ? 'overflow-y-scroll h-[500px] scrollbar-custom pt-2 pr-2' : pagesName === 'upcomming' ? 'w-full h-full p-0 m-0' : 'overflow-x-scroll scrollbar-custom scrollbar-hide pt-1 lg:pb-2'}">
                    <div class="movies-or-tv gap-2 ${judul === 'Now Playing Movies' || pagesName === 'upcomming' || pagesName === 'trending' || pagesName === 'on-air' || pagesName === 'top-rated-tv' || pagesName === 'popular-tv-show' || pagesName === 'genre' ? 'grid grid-cols-2 md:grid-cols-4 lg:grid-cols-5' : 'flex'}">
    
                    </div>
                </div>
                ${judul === 'Now Playing Movies' || pagesName === 'upcomming' || pagesName === 'trending' || pagesName === 'on-air' || pagesName === 'popular-tv-show' || pagesName === 'top-rated-tv' || pagesName === 'genre' ? `
                    <div class="pagination text-center mt-2 flex items-center justify-center gap-2">
                        <button class="prev bg-red-600 text-slate-50 hover:bg-red-700 text-sm rounded py-1 px-2" title="Previous Page">Prev</button>
                        <input class="number-of-page text-slate-50 w-[50px] h-[28px] border-none text-center text-sm rounded" type="text" name="" id="" value="1" disabled>
                        <button class="next bg-red-600 text-slate-50 hover:bg-red-700 text-sm rounded py-1 px-2" title="Next Page">Next</button>
                    </div>
                ` : ''}
            </div>  
        `;

const cardTemplate = (data, pagesName) => `
          <a class="card min-w-[150px] max-w-[200px] w-full h-full relative rounded transition-all hover:-translate-y-1" href="/#/${data.media}/${data.id}" title="${data.title ? data.title : data.name}">
              <picture>
                  <source media="(max-width: 600px)" srcset="https://image.tmdb.org/t/p/w185${data.poster_path ? data.poster_path : data.still_path ? data.still_path : data.profile_path}">
                  ${data.still_path ? `
                    <img class="object-cover h-[200px] w-full text-slate-50 rounded" src="https://image.tmdb.org/t/p/w300${data.poster_path ? data.poster_path : data.still_path ? data.still_path : data.profile_path}" alt="poster of ${data.title ? data.title : data.name}" loading="lazy">
                  ` : `
                    <img class="object-fill w-full min-h-[230px] max-h-[300px] h-full text-slate-50 rounded" src="https://image.tmdb.org/t/p/w342${data.poster_path ? data.poster_path : data.profile_path}" alt="poster of ${data.title ? data.title : data.name}" loading="lazy">
                  `}
              </picture>
              ${data.media === 'person' ? `
                <div class="name absolute bottom-0 left-0 w-full h-fit overflow-hidden bg-slate-800/50 backdrop-blur-sm flex items-center justify-center lg:h-0">
                    ${data.credit_id ? `
                        <div class="text-center py-2 lg:py-0">
                            <span class="text-xs font-bold block">${data.character}</span>
                            <i class="text-xs block">${data.name}</i>
                        </div>
                    ` : `<span class="text-xs font-medium py-2">${data.name}</span>`}
                </div>
              ` : pagesName === 'genre' ? `
                <span class="bg-red-600 text-slate-50 font-medium px-2 py-1 absolute top-0 right-0 text-xs rounded-tr-[inherit] rounded-bl-[inherit]">${String(data.vote_average).substring(0, 3)}/10</span>
                <span class="bg-red-600 text-slate-50 font-medium px-2 py-1 absolute bottom-0 left-0 text-xs rounded-bl-[inherit] rounded-tr-[inherit]">${data.media.charAt(0).toUpperCase() + data.media.slice(1)}</span>
              ` : `
                <span class="bg-red-600 text-slate-50 font-medium px-2 py-1 absolute top-0 right-0 text-xs rounded-tr-[inherit] rounded-bl-[inherit]">${String(data.vote_average).substring(0, 3)}/10</span>
              `}
          </a>
      `;

